body {
  margin: 0;
  padding: 0;
  background-color: #231e23;
  color: #f4f4f4;
  font-family: 'Open Sans', sans-serif;
}

#root {
  margin: 0 auto;
  position: relative;
  min-height: 100vh;
}
.header {
  display: flex;
  align-items: center;
  margin-top: 20px;
  margin-bottom: 40px;
  flex-direction: column;
  font-family: 'Permanent Marker', cursive;
}


.title {
  color: #ffffff;
  text-align: center;
  margin: 10px 0 0 8px;
  margin-left: 8px;
  font-size: 3rem;
  font-weight: 700;
}
.TodoCounter {
  font-size: 24px;
  text-align: center;
  margin: 0;
  color: #DEEEEA;
  padding-bottom: 24px;
  font-weight: 400;
  font-family: 'Permanent Marker', cursive;
}
.TodoSearch {
    background: #39A6A3;
    border-radius: 16px;
    border: none;
    margin: 34px 24px;
    height: 64px;
    width: calc(100% - 62px);
    font-size: 24px;
    text-align: center;
    font-family: 'Ubuntu';
    font-weight: 500;
    color: #DEEEEA;
    box-shadow: 0px 4px 20px 0px rgba(54, 54, 54, 0.397);
    align-self: center;
    outline: none;
  }
  
  .TodoSearch::-webkit-input-placeholder {
    color: #deeeead5;
    font-family: 'Ubuntu';
    font-weight: 400;
  }
  
  .TodoSearch:-ms-input-placeholder {
    color: #deeeead5;
    font-family: 'Ubuntu';
    font-weight: 400;
  }
  
  .TodoSearch::placeholder {
    color: #deeeead5;
    font-family: 'Ubuntu';
    font-weight: 400;
  }
ul {
    width: 90%;
    margin: 0 auto;
    padding: 0 0 56px 0;
    list-style: none;
  }
.TodoItem {
  background-color: #FAFAFA;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 24px;
  box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);
  border-radius: 16px;
  color: #333;
  word-wrap: break-word;
}

.TodoItem-p {
  margin: 24px 0;
  width: calc(100% - 120px);
  font-size: 18px;
  line-height: 24px;
  font-weight: 400;
}
.TodoItem-p--complete {
  text-decoration: line-through;
}

.Icon {
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 48px;
  width: 48px;
  font-size: 24px;
  font-weight: bold;
}

.Icon-check {
  margin-left: 4px;
}
.Icon-check--active {
  color: #4caf50;
}
.Icon-check:hover {
  color: #39A6A3;
}

.Icon-delete {
  padding-right: 4px;
}
.Icon-delete:hover {
  color: red;
}
.container {
  display: flex;
  height: 200px;
  justify-content: center;
  align-items: flex-end;
  margin-bottom: 10px;
}

.CreateTodoButton {
  background-color: #61DAFA;
  box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
  border: none;
  border-radius: 50%;
  cursor: pointer;
  font-size: 50px;
  /* position: absolute;
  bottom: 10%;
  right: 48%;  */
  font-weight: bold;
  color: #FAFAFA;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 64px;
  width: 64px;

  -webkit-transform: rotate(0);

          transform: rotate(0);
  transition: .3s ease;
}

.CreateTodoButton:hover {
  -webkit-transform: rotate(224deg);
          transform: rotate(224deg);
}
.ModalBackground {
    background: rgba(32,35,41,.8);
    position: fixed;
    top: -10px;
    left: -10px;
    right: -10px;
    bottom: -10px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
  }
form {
  width: 90%;
  max-width: 300px;
  background-color: #fff;
  padding: 33px 40px;
  display: flex;
  align-content: center;
  flex-direction: column;
}

label {
  text-align: center;
  font-weight: bold;
  font-size: 22px;
  margin-bottom: 26px;
  color: #1E1E1F;
}

textarea {
  background-color: #F9FBFC;
  border: 2px, solid #202329;
  border-radius: 2px;
  box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
  color: #1E1E1F;
  font-size: 20px;
  text-align: center;
  padding: 12px;
  height: 96px;
  width: calc(100%-25px);
}

textarea::-webkit-input-placeholder {
color: #a5a5a5;
font-family: 'Montserrat';
font-weight: 400;
}

textarea:-ms-input-placeholder {
color: #a5a5a5;
font-family: 'Montserrat';
font-weight: 400;
}

textarea::placeholder {
color: #a5a5a5;
font-family: 'Montserrat';
font-weight: 400;
}

textarea:focus {
  outline-color: #61DAFA;
}

.TodoForm-buttonContainer {
  margin-top: 14px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.TodoForm-button {
  cursor: pointer;
  display: inline-block;
  font-size: 20px;
  color: #202329;
  font-weight: 400;
  width: 120px;
  height: 48px;
  border-radius: 2px;
  border: none;
  font-family: 'Montserrat';
}

.TodoForm-button-add {
  background-color: #61DAFA;
  box-shadow: 0px 5px 25px rgba(97, 218, 250, 0.5);
}

.TodoForm-button-cancel {
  background: transparent;
}
  
.empty-msg {
  width: 90%;
  margin: 50px auto 0 auto;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.empty-msg > h1 {
  color: #BF1363;
  font-size: 2.5rem;
  margin: 0;
}
.empty-subtitle {
    display: flex;
    flex-direction: row;
}
.empty-subtitle > p {
  font-size: 18px;
  font-weight: 500;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin: 10px 0;
  color: #333;
}
.add-button {
  background-color: #BF1363;
  box-shadow: 0px 0px 12px rgba(191, 19, 99, 0.5);
  border: none;
  border-radius: 50%;
  font-size: 3rem;
  font-weight: bold;
  color: #DEEEEA;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 30px;
  width: 30px;
  margin: 0 5px;
} 
html {
    font-size: 62.5%;
}

